import {Component, OnInit} from '@angular/core';
import {ErrorService,MessageTypes,ErrorMsg} from "../../globals/services/error.service";
import {APIService} from "../../globals/services/API.service";
import {userType} from "./user.type";
import {globals} from "../../utils/global.constats";
import {AuthService} from  '../../globals/services/auth.service';
import {Privilegium} from  '../../globals/TYPES';

import {HtmlcontentsService} from  '../../globals/services/htmlcontents.service';
import { Hunkhtml} from '../../directives/hunkhtml.directive';
import { CkeditorComponent } from '../../components/ckeditor/ckeditor.component';
import { HunkToolbarComponentComponent } from '../../components/hunk-toolbar-component/hunk-toolbar-component.component';

@Component({
  moduleId: module.id,
  selector: 'regisztracio-view',
  templateUrl: 'htmls/regisztracio.html',
  providers: [],
  directives:[Hunkhtml,CkeditorComponent,HunkToolbarComponentComponent]

})

export class RegisztracioViewComponent implements OnInit {
  currentUser:userType = {name: '', email: '', password: ''};
  submitButtonCaption = 'Regisztráció';
  content:string='regisztracio_mielott';

  editor2show:boolean=false;
  constructor(
    private _ErrorService:ErrorService,
    private _APIService:APIService,
    private _AuthService:AuthService,
    public _HtmlcontentsService:HtmlcontentsService) {
    console.log(this._HtmlcontentsService);
  }

;
  onRegiser() {

    this._APIService.getResponse('api/user/', this.currentUser).subscribe(
        data=> {
        console.log(data);
        this._ErrorService.errorClear();
        if (data.message) {
          let error:ErrorMsg = {
            type: MessageTypes.mtDanger,
            msg: data.message.errors
          }
          this._ErrorService.setErrorMsg(error);
        }

        else this.login();


      },
        error=> {
        console.log(error);

      })
  }

  login() {


    var loginAs = {
      grant_type: this._AuthService.grantType(),
      client_id: globals.client_id,
      client_secret: globals.client_secret,
      username: this.currentUser.email,
      password: this.currentUser.password


    };


    this._APIService.getResponse('oauth/acces_token', loginAs).subscribe(
        data=> {
        console.log(data);
        if (data.msg == 'login success') {
          this._AuthService.setUser({access_token: data.oauth.access_token});
          // window.location.href += "/#/ingatlanok";
          window.location.reload();

        }


      }
    )


  }

  ngOnInit() {
    if (this._AuthService.logged && this._AuthService.userType!=Privilegium.prAdmin)
      window.location.href = "#/ingatlanok";

  }
  onChange2View(viewMode){
    this.editor2show=(viewMode=='edit')?true:false;
  }

  onValueChanged2(content){
    this._HtmlcontentsService.contents.regisztracio_mielott.content=content;
  }

  save2Content(){

    this._HtmlcontentsService.setContent( this._HtmlcontentsService.contents.regisztracio_mielott.id,this._HtmlcontentsService.getContent('regisztracio_mielott'));
    this.editor2show=false;
  }

}
